.TodoInsert {
  position: fixed;
  display: flex;
  background: #e6e6e6;
  bottom: 20px;
  width: 650px;
  border-radius: 50px;
  padding-left: 20px;
  margin: 0 17px;
  box-sizing: border-box;
  box-shadow: 2px 2px 2px 2px #d3d3d3;
}

input {
  background: none;
  outline: none;
  border: none;
  padding: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.5;
  color: #22b8cf;
  &::placeholder {
    color: #8f8f8f;
  }
  flex: 1;
}

button {
  background: #868e96;
  outline: none;
  border: none;
  color: white;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  border-radius: 0 50px 50px 0;
  transition: background 0.1s ease-in;
  &:hover {
    background: #adb5bd;
  }
}
